import Vue from 'vue';
var vm = new Vue();
// 成功弹框提示
export const successAlert = (msg)=>{
  vm.$message({
    message: msg,
    type: 'success'
  });
}

//警告弹框提示
export const warningAlert = (msg)=>{
  vm.$message({
    message: msg,
    type: 'warning'
  });
}

//错误弹框提示
export const errorAlert = (msg)=>this.$message.error(msg);



